.profile-section {
  display: flex;
  justify-content: center;
  padding: 20px; /* Reduce padding around the section */
}

.profile-content {
  display: flex;
  align-items: flex-start; /* Align items at the top */
  background-color: #fff; /* Background for the card */
  padding: 20px; /* Reduce padding inside the card */
  border-radius: 15px; /* Rounded corners */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Softer shadow for depth */
  max-width: 800px; /* Reduce max-width for a more compact look */
  width: 100%;
}

.profile-pic {
  width: 150px; /* Slightly reduce image size */
  height: 150px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 20px; /* Space between image and text */
}

.profile-text {
  flex: 1;
}

.profile-text h2 {
  font-size: 1.75rem; /* Slightly reduce font size */
  color: #333;
  margin-bottom: 5px; /* Reduce margin */
}

.profile-text p {
  color: #666;
  font-size: 1rem; /* Adjust font size for better readability */
  margin-bottom: 15px; /* Reduce margin */
}

.profile-text h1 {
  font-size: 1.25rem; /* Slightly reduce font size */
  color: #444;
  line-height: 1.4; /* Tighten line spacing */
}

.highlight {
  color: #e34c26; /* Accent color for keywords */
}

/* Optional: Add responsiveness */
@media (max-width: 768px) {
  .profile-content {
    flex-direction: column; /* Stack image and text vertically on smaller screens */
    text-align: center; /* Center align text */
  }

  .profile-pic {
    margin-right: 0;
    margin-bottom: 15px; /* Add space below the image when stacked */
  }
}
